// 引入防抖
import {debounce} from '../../common/js/common.js'
// 引入定位
import {addressData} from '../../common/network/map.js'
// 引入SDK核心类
var QQMapWX = require('../../common/QQmap/qqmap-wx-jssdk.js');
var qqmapsdk;

export default{
	name:'Search',
	data(){
		return{
			address:'请定位',
			isSearch:false,
			city:[]
		}
	},
	mounted(){
		this.location()
	},
	methods:{
		location(){
			addressData()
				.then(res => {
					this.address = res.result.ad_info.city
					console.log(res)
				})
				.catch(err => {
					console.log('用户拒绝定位')
				})
		},
		tapResult(city){
			this.myNavigateTo(city)
		},
		tapAddress(){
			let city = this.address
			this.myNavigateTo(city)
		},
		tapCity(item){
			this.myNavigateTo(item)
		},
		// 公共跳转函数
		myNavigateTo(city){
			// vuex传值city
			this.$store.commit('setCity',city)
			uni.navigateBack({
				delta:1
			})
		},
		onSearch(){
			console.log('search')
			this.isSearch = true
		},
		closeSearch(){
			this.isSearch = false
		},
		// 实时搜索城市
		searchAddress(e){
			console.log(e.detail.value)
			let key = e.detail.value
			// 实例化API核心类
			qqmapsdk = new QQMapWX({
				key: 'IOBBZ-KX4LI-ITAGZ-5SJDP-J43CE-DIFPX'
			});
			qqmapsdk.getSuggestion({
				keyword:key,
				success:(res => {
					console.log(res)
					this.city = res.data
				}),
				fail:(err => {
					console.log(err)
				})
			})
		}
	}
}
